    margin: 0 5px !important;
    border: 2px solid #111 !important;
    transition: 0.2s ease !important;
}
#nav-colors .btn:hover, #font-colors .btn:hover, #edges .btn:hover {
    border: 2px solid white !important;
}
#nav-colors #pink {
